/**
 *
 * @param {String} data.shortCode
 * @param {String} [data.accountTypeId]
 * @param {Boolean} data.queryChildren
 * @param {Number} data.pageNo
 * @param {Number} data.pageSize
 * @example
 * 参数说明:
 * a.若界面上Short Code为空，则查询当前组织和子组织的账户信息，queryChildren为true，使用当前组织的shortCode填充Short Code查询条件
 * b.若界面上Short Code非空，则查询输入的Short Code的账户信息，queryChildren为false
 *
 */
export function queryBalanceOverviewPage(data) {
  return request({
    url: '/fintech/v1/organization/queryBalanceOverviewPage',
    method: 'post',
    data,
  });
}

