<template>
  <div class="panel-detail">
    <template v-if="basicInfo">
      <basic-info-group :groups="groups" :basic-info="basicInfo"></basic-info-group>
    </template>
    <el-tabs tab-position="top" class="tabs" v-model="chargeType">
      <el-tab-pane label="Fee" :name="CHARGE_TYPES.FREE">
        <table-page
          :key="CHARGE_TYPES.FREE"
          style="margin-top: 20px"
          size="small"
          :isSingleCheck="false"
          :has-pagination="false"
          :columns="defaultColumns"
          :outSideTableData="feeList"
        >
          <template slot-scope="scope" slot="band">
            <span>{{ generateGroupFieldsText(scope.data.row.bandBegin, scope.data.row.bandEnd) }}</span>
          </template>
        </table-page>
      </el-tab-pane>
      <el-tab-pane label="Commission" :name="CHARGE_TYPES.COMMISSION">
        <table-page
          :key="CHARGE_TYPES.COMMISSION"
          style="margin-top: 20px"
          size="small"
          :isSingleCheck="false"
          :has-pagination="false"
          :columns="defaultColumns"
          :outSideTableData="commissionList"
        >
          <template slot-scope="scope" slot="band">
            <span>{{ generateGroupFieldsText(scope.data.row.bandBegin, scope.data.row.bandEnd) }}</span>
          </template>
        </table-page>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
import BasicInfoGroup from '@/views/identifier-center/pages/organization/components/basic-info-group';
const CHARGE_TYPES = {
  FREE: 'free',
  COMMISSION: 'Commission',
};
export default {
  name: 'ChargeProfilePanelDetail',
  components: { BasicInfoGroup, TablePage },

  props: {
    detail: {
      type: Object,
      require: true,
    },
  },
  inject: ['generateGroupFieldsText'],

  data() {
    return {
      data: null,
      loaded: false,
      CHARGE_TYPES,
      chargeType: CHARGE_TYPES.FREE,
      groups: [
        {
          label: this.$t('IDC.chargeProfile.basicInfo'),
          list: [
            {
              label: this.$t('IDC.chargeProfile.chargeRuleName'),
              name: 'ruleName',
            },
            {
              label: this.$t('IDC.chargeProfile.priority'),
              name: 'priority',
            },
            {
              label: this.$t('IDC.chargeProfile.effectivePeriod'),
              name: 'effectivePeriod',
            },
          ],
        },
        {
          label: null,
          list: [
            {
              label: this.$t('common.label.description'),
              name: 'ruleDesc',
            },
          ],
        },
        {
          label: null,
          list: [
            {
              colSpan: 24,
              label: this.$t('IDC.chargeProfile.chargeCondition'),
              name: 'chargeCondition',
            },
          ],
        },
      ],

      defaultColumns: [
        { label: this.$t('IDC.chargeProfile.reasonType'), prop: 'reasonType', align: 'left', minWidth: 300 },
        { label: this.$t('IDC.chargeProfile.currency'), prop: 'currency', align: 'left', minWidth: 100 },
        { label: this.$t('IDC.chargeProfile.band'), prop: 'band', align: 'left', minWidth: 100, __slotName: 'band' },
        { label: this.$t('IDC.chargeProfile.flat'), prop: 'flat', align: 'left', minWidth: 100 },
        { label: this.$t('IDC.chargeProfile.percentage'), prop: 'percentage', align: 'left', minWidth: 100 },
        { label: this.$t('IDC.chargeProfile.minVallue'), prop: 'minVallue', align: 'left', minWidth: 100 },
        { label: this.$t('IDC.chargeProfile.maxValue'), prop: 'maxValue', align: 'left', minWidth: 100 },
      ],
    };
  },
  computed: {
    basicInfo() {
      if (!this.detail) {
        return null;
      }
      return this.detail.basicInfo;
    },
    feeList() {
      if (!this.detail) {
        return [];
      }
      return this.detail.feeList || [];
    },
    commissionList() {
      if (!this.detail) {
        return [];
      }
      return this.detail.commissionList || [];
    },
  },
  watch: {},
  created() {},

  methods: {},
};
</script>

<style lang="scss" scoped>
.tabs {
  /deep/ {
    .el-tabs__item {
      color: $--text_color--strong;
      font-size: 16px;
      font-weight: 700;
    }
    .el-tabs__item.is-active {
      color: $--primary_color;
    }

    .el-tabs__item:hover {
      color: $--primary_color;
    }
    .el-tabs__active-bar {
      background-color: $--primary_color;
    }
    .el-tabs__nav-wrap::after {
      height: 1px;
      background-color: $--fill_color--regular;
    }
    .el-tabs__content {
      padding: 0 !important;
    }
  }
}
</style>
