<template>
  <div class="tabs" :class="customClass">
    <div class="tabs__menu">
      <div
        v-for="(tab, index) in tabs"
        class="tabs__menu-item"
        :class="{ 'tabs__menu-item--active': active === tab.name }"
        :key="index"
        @click="checkMenuTab(tab.name)"
      >
        {{ tab.label }}
      </div>
    </div>
    <div class="tabs-container">
      <transition-group name="load-transform" mode="out-in">
        <div class="tabs__content" :class="active" v-for="tab in tabs" :key="tab.name" v-show="active === tab.name">
          <slot :name="tab.__slotName"></slot>
        </div>
      </transition-group>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TabsVertical',
  props: {
    tabs: {
      type: Array,
      default: () => [],
    },
    activedName: {
      type: String,
      required: true,
    },
    customClass: {
      type: String,
      default: () => '',
    },
  },
  data() {
    return {
      active: this.activedName,
    };
  },
  watch: {
    activedName: {
      handler() {
        this.checkMenuTab(this.activedName);
      },
      immediate: true,
    },
  },
  methods: {
    checkMenuTab(name) {
      this.active = name;
      this.$emit('update:activedName', name);
      this.$emit('tab-change', name);
    },
  },
};
</script>

<style lang="scss" scoped>
.tabs {
  display: flex;

  border-top: 1px solid $--fill_color--regular;
  .tabs__menu {
    width: 268px;
    padding-left: 3px;
    padding-right: 5px;
    box-shadow: inset -1px 0 1px $--fill_color--regular;
    font-size: 16px;
    box-sizing: border-box;

    .tabs__menu-item {
      height: 32px;
      margin-top: 4px;
      line-height: 32px;
      color: $--text_color--strong;
      background-color: $base-menu-background;
      cursor: pointer;
      text-indent: 16px;
    }
    .tabs__menu-item--active {
      background-color: $--fill_color--regular;
    }
  }
  .tabs-container {
    flex: 1;
    height: max-content;

    .tabs__content {
      padding: 20px;
    }
  }

  /* load-transform */
  .load-transform-leave-active,
  .load-transform-enter-active {
    transition: all 0.3s;
  }

  .load-transform-enter {
    transform: translateX(-30px);
    opacity: 0;
  }

  .load-transform-leave-to {
    transform: translateX(30px);
    opacity: 0;
  }
}
</style>
