<template>
  <div class="tabs-panel" :class="customClass" v-loading="loading">
    <slot :detail="data"></slot>
  </div>
</template>

<script>
export default {
  name: 'ChargeProfilePanel',
  provide() {
    return {
      panel: this,
    };
  },
  props: {
    tab: {
      type: Object,
      require: true,
    },
    actived: {
      type: Boolean,
      required: true,
    },
    customClass: {
      type: String,
      default: () => '',
    },
    panelName: {
      type: String,
      required: true,
    },
    getData: {
      type: Function,
      required: true,
    },
  },
  data() {
    return {
      data: null,
      loaded: false,
      loading: false,
    };
  },
  watch: {
    actived: {
      handler() {
        if (this.actived) {
          if (!this.loaded) {
            this.setState();
          }
        }
      },
      immediate: true,
    },
  },
  created() {},

  methods: {
    async setState() {
      const tab = this.tab;
      this.loading = true;
      this.data = await this.getData(tab);
      this.loading = false;
      this.loaded = true;
    },
  },
};
</script>
