import { dateFormat } from '@/common/utils/date-time-utils';

export async function exportUtils(callBack, data, opt) {
  const { fileType, blobType = 'application/ms-excel' } = opt;
  const res = await callBack(data);
  if (res.type === 'application/json') {
    const reader = new FileReader();
    reader.readAsText(res);
    const _this = this;
    reader.onload = function () {
      const errorData = JSON.parse(reader.result);
      _this.$modal.msgError(errorData.header.responseDesc);
    };
  } else {
    const rollupStart = new Date();
    const date = dateFormat(rollupStart, 'YYYYMMDDHHmmss');
    let temp = 'FINANCIAL_RECONCILIATION_' + date + '.' + fileType.toLowerCase();
    if (res.headers) {
      temp = res.headers['content-disposition'].split(';')[1].split('filename=')[1];
    }
    const blob = new Blob([res], { type: blobType }); // 解析后端返回的乱码
    const elink = document.createElement('a');
    elink.download = temp;
    elink.style.display = 'none';
    elink.href = URL.createObjectURL(blob);
    document.body.appendChild(elink);
    elink.click();
    URL.revokeObjectURL(elink.href);
    document.body.removeChild(elink);
  }
}
