<template>
  <div>
    <basic-info-group-item v-for="(group, index) in configList" :key="index" :group="group" />
  </div>
</template>
<script>
import BasicInfoGroupItem from '@/views/identifier-center/pages/organization/components/basic-info-group-item';
import { deepClone } from '@/views/identifier-center/pages/organization/utils/common';
export default {
  name: 'BasicInfoGroup',
  components: { BasicInfoGroupItem },
  props: {
    basicInfo: {
      type: Object,
      default: () => ({}),
    },
    groups: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {};
  },

  computed: {
    configList() {
      const configMap = this.basicInfo;
      let groups = deepClone(this.groups);
      groups = groups.map((group) => {
        group.list = group.list.map((field) => {
          const name = field.name;
          const label = field.label;
          return {
            ...field,
            label,
            value: configMap[name] || '-',
          };
        });

        return group;
      });
      return groups;
    },
  },
};
</script>
