<template>
  <div class="profile">
    <portal-header :projectObj="titleConfig" :showBottomLine="true"></portal-header>
    <div class="panel-block">
      <el-dropdown @command="exportData">
        <el-button type="primary" size="small" :disabled="isExport" v-preventReClick>
          {{ $t('common.button.export') }}
          <i class="el-icon-arrow-down el-icon--right" style="font-size: 12px"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="XLS">Excel</el-dropdown-item>
          <el-dropdown-item command="XLSX">Excel 2007</el-dropdown-item>
          <el-dropdown-item command="CSV">CSV</el-dropdown-item>
          <el-dropdown-item command="PDF">PDF</el-dropdown-item>
          <el-dropdown-item command="TXT">TXT</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>

      <tabs-vertical
        ref="tabsRef"
        :tabs="tabs"
        :activedName.sync="tabName"
        @set-default-tab="setDefaultTab"
        customClass="panel-tabs"
      >
        <template v-for="tab in tabs" :slot="tab.name">
          <charge-profile-panel
            :key="tab.name"
            :tab="tab"
            :panelName="tab.name"
            :actived="tab.name === tabName"
            :get-data="getPanelData"
          >
            <template slot-scope="scope">
              <charge-profile-panel-detail :detail="scope.detail" />
            </template>
          </charge-profile-panel>
        </template>
      </tabs-vertical>
    </div>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import TabsVertical from '@/views/identifier-center/pages/organization/profile/tabs-vertical';
import { dealApiRes, deepClone } from '@/views/identifier-center/pages/organization/utils/common';
import {
  ChargeProfileRuleList,
  downloadChargeProfile,
  queryChargeProfileDetail,
} from '@/views/identifier-center/api/api-organization';
import ChargeProfilePanel from '@/views/identifier-center/pages/organization/profile/charge-profile-panel';
import ChargeProfilePanelDetail from '@/views/identifier-center/pages/organization/profile/charge-profile-panel-detail';
import { exportUtils } from '@/common/utils/export-utils';
import { dateFormat } from '@/common/utils/date-time-utils';

export default {
  name: 'rule-profile-detail',
  components: {
    ChargeProfilePanelDetail,
    ChargeProfilePanel,
    TabsVertical,
    PortalHeader,
  },

  provide() {
    return {
      generateGroupFieldsText: this.generateGroupFieldsText,
    };
  },

  data() {
    return {
      titleConfig: {
        title: this.$t('IDC.chargeProfile.titleConfig.title'),
        desc: this.$t('IDC.chargeProfile.titleConfig.desc'),
      },
      isExport: false,
      tabName: '',
      cache: new Map(),
      tabs: [],
    };
  },

  computed: {},

  created() {
    this.init();
  },

  methods: {
    async init() {
      await this.getMenus();
    },
    setDefaultTab() {},
    async getMenus() {
      const params = this.getRouteParam();

      if (!params) {
        return;
      }
      const id = params.id;
      if (!id) {
        return;
      }
      let memus = await dealApiRes(ChargeProfileRuleList, 'list', id);
      memus = memus || [];
      let tabs = memus.map((item) => {
        item.name = item.ruleName;
        item.label = item.ruleName;
        item.__slotName = item.ruleName;
        return item;
      });
      if (tabs.length) {
        this.tabName = tabs[0].name;
      }
      this.tabs.length = 0;
      this.tabs.push(...tabs);
    },
    async getPanelData(tab) {
      if (!tab) {
        return null;
      }

      const cache = this.cache;
      const data = cache.get(tab);

      if (data) {
        return data;
      }
      if (!tab.cfgUniqueId || !tab.ruleId) {
        return null;
      }

      const params = {
        uniqueId: tab.cfgUniqueId,
        ruleId: tab.ruleId,
      };
      let detail = (await this.queryChargeProfileDetail(params)) || {};
      detail = deepClone(detail);
      if (tab.ruleId == '-1') {
        detail.basicInfo = null;
      } else {
        const basicInfo = deepClone(tab);
        // 特殊字段处理
        const applyData = dateFormat(basicInfo.applyDate);
        const expireDate = dateFormat(basicInfo.expireDate);
        const effectivePeriod = this.generateGroupFieldsText(applyData, expireDate);
        basicInfo.chargeCondition = `if((Credit.Segments contain [${basicInfo.chgCondition}])) return true; else return false`;
        basicInfo.effectivePeriod = effectivePeriod;
        detail.basicInfo = basicInfo;
      }
      cache.set(tab, detail);

      return detail;
    },
    generateGroupFieldsText(pre, next) {
      if (!pre && !next) {
        return '-';
      }
      pre = pre || '';
      next = next || '';
      return pre + ' ~ ' + next;
    },
    async queryChargeProfileDetail({ uniqueId, ruleId }) {
      if (!uniqueId || !ruleId) {
        return null;
      }
      const params = { uniqueId, ruleId };
      try {
        const result = await queryChargeProfileDetail(params);
        if (result && result.header && result.header.success) {
          return result;
        }
      } catch (e) {}
      return null;
    },

    exportData(fileType) {
      const tab = this.tabs.find((item) => item.name === this.tabName);
      if (!tab || !tab.cfgUniqueId || !tab.ruleId || !fileType) {
        return null;
      }
      let paramObj = {
        uniqueId: tab.cfgUniqueId,
        ruleId: tab.ruleId,
        fileType: fileType,
      };

      exportUtils(downloadChargeProfile, paramObj, { fileType });
    },
    getRouteParam() {
      let params = this.$route.query.data;
      if (!params) {
        return null;
      }
      params = decodeURIComponent(params);
      if (!params) {
        return null;
      }
      return JSON.parse(params);
    },
  },
};
</script>

<style lang="scss" scoped>
.profile {
  .panel-block {
    height: 100%;
    padding: 20px;
    background: $base-menu-background;

    .panel-block__title {
      font-weight: 700;
    }
  }

  .panel-tabs {
    min-height: calc(100vh - 200px);
    height: max-content;
    margin-top: 20px;
  }

  .padding-l-r-20 {
    padding-left: 20px;
    padding-right: 20px;
  }
}
</style>
